import React from 'react';
import { TopActionsComponent } from "./top_actions.component";
import { ProductsTable } from "./product_table.component";
import $ from 'jquery';
//window.jQuery = jQuery$;
//window.$ = jQuery;
//const $ = window.jQuery;

// component that contains all the logic and other smaller components
// that form the Read Products view

export class ReadProductsComponent extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            products: []
        };

    }
    /*getInitialState() {
        return {
            products: []
        };
    }*/

    // on mount, fetch all products and stored them as this component's state
    componentDidMount() {

        this.serverRequest = $.get("./mock/read.json", function (products) {
            console.log(products)
            this.setState({
                products: [
                    {
                        "name": "Vishal",
                        "description": "Chakri not stable",
                        "price": 1,
                        "category_name": "Cat 1"
                    },
                    {
                        "name": "Sagar",
                        "description": "Chakri not stable",
                        "price": 2,
                        "category_name": "Cat 2"
                    },
                    {
                        "name": "Chakri",
                        "description": "Chakri not stable",
                        "price": 3,
                        "category_name": "Cat 3"
                    },
                    {
                        "name": "Jayesh",
                        "description": "Chakri not stable",
                        "price": 4,
                        "category_name": "Cat 4"
                    },
                    {
                        "name": "Manikanta",
                        "description": "Chakri not stable",
                        "price": 5,
                        "category_name": "Cat 5"
                    }
                ]
            });
        }.bind(this));
    }

    // on unmount, kill product fetching in case the request is still pending
    componentWillUnmount() {
        this.serverRequest.abort();
    }

    // render component on the page
    render() {
        // list of products
        var filteredProducts = this.state.products;
        $('.page-header h1').text('Read Products');

        return (
            <div className='overflow-hidden'>
                <TopActionsComponent changeAppMode={this.props.changeAppMode} />

                <ProductsTable
                    products={filteredProducts}
                    changeAppMode={this.props.changeAppMode} />
            </div>
        );
    }
}


